.main-footer__social {
  padding: 20px 74px;
}

.social__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (min-width: 768px) {
  .main-footer__social {
    padding: 0;
  }

  .social__list {
    width: 132px;
  }
}
